@import "./reset.scss";

@keyframes fadeInAnimation {
    0% {
        opacity: 0;
    }

    100% {
        opacity: 1;
    }
}

$bg: rgb(255, 246, 224);
$bg-font: rgb(255, 228, 158);
$primary: rgb(255, 176, 57);
$black: rgb(43, 43, 43);
$white: rgb(255, 255, 255);

:root {
    font-family: 'Courier New', Courier, monospace;
    color: $black;

    button {
        background-color: $primary;
        border: 0px;
        color: $white;
        padding: 10px 20px;
        border-radius: 10px;
        cursor: pointer;
    }
}

body {
    animation: fadeInAnimation ease 3s;
    animation-iteration-count: 1;
    animation-fill-mode: forwards;
}

.app {
    background-color: $bg;

    >div {
        height: 100vh;
        position: relative;
    }

    .watermark {
        z-index: 0;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        opacity: 0.5;
        color: $bg-font;
        font-size: 10vw;
    }

    .main {
        display: flex;
        align-items: center;
        justify-content: center;

        .container {
            width: 80vw;
            display: flex;
            align-items: center;
            justify-content: center;
            flex-wrap: wrap;

            >div {
                margin: 0px auto;
                padding: 20px;
                transition: transform 3s ease;
            }

            .left {}

            .right {}
        }
    }

    .skills {
        display: flex;
        align-items: center;
        justify-content: center;

        .container {
            z-index: 1;
            width: 80vw;
            display: flex;
            align-items: center;
            justify-content: center;
            shape-outside: radial-gradient(farthest-side at right, transparent calc(100% - var(--p)), #fff 0);


            .skills {
                width: 50vw;
                display: flex;
                flex-wrap: wrap;

                span {
                    margin: 10px;
                }
            }
        }
    }

    .portfolio {

        .container {
            .item {
                z-index: 1;
                height: 100vh;
                display: flex;
                flex-direction: column;

                .body {
                    display: flex;
                    flex: 1;
                    padding: 0px 10px;

                    .left {
                        cursor: pointer;
                        display: flex;
                        align-items: center;
                    }

                    .content {
                        display: flex;
                        align-items: center;
                        justify-content: center;
                        flex: 1;
                        animation: fadeInAnimation ease 1s;
                        animation-fill-mode: forwards;

                        img {
                            height: 75vh;
                            width: 75vw;
                            object-fit: contain;
                        }
                    }

                    .right {
                        cursor: pointer;
                        display: flex;
                        align-items: center;
                    }
                }

                .footer {
                    display: flex;
                    align-items: center;
                    flex-direction: column;

                    .nav {
                        display: flex;
                        flex-direction: row;
                        align-items: center;
                        justify-content: center;

                        .dot {
                            background-color: $bg-font;
                            height: 10px;
                            width: 10px;
                            margin: 10px;
                            border-radius: 50%;

                            &.active {
                                background-color: $primary;
                            }
                        }
                    }
                }

                .disabled {
                    opacity: 0.1;
                }
            }
        }
    }
}